<template>
<div class="dangjian">
  <div class="inputBx"><input type="submit" value="添加" @click="show_Post_Edit"></div>
  <div class="box">
    <table class="My_table">
      <tr>
        <th>编号</th>
        <th>主题</th>
        <th>等级</th>
        <th>开始时间</th>
        <th>结束时间</th>
        <th>操作</th>
      </tr>
      <tr v-for="(item, index) in data" :key="index">
        <td>{{item.this_id}}</td>
        <td><a href="#" @click="gotoInfo(item.id)">{{item.title}}</a></td>
        <td>{{item.level}}</td>
        <td>{{(item.start_date).slice(0, 10)}}</td>
        <td>{{(item.end_date).slice(0, 10)}}</td>
        <td><a href="#"><i class="fa fa-pencil-square-o" aria-hidden="true" @click="show_Put_Edit"></i></a><span>&nbsp;||&nbsp;</span>
          <a href="#"><i class="fa fa-trash" aria-hidden="true"></i></a></td>
      </tr>
    </table>
  </div>
  <div class="My_edit" id="My_edit">
    <div class="My_form">
      <h2 id="title">添加</h2>
      <div class="inputBx">
        <input type="text" placeholder="编号" v-model="this_id">
      </div>
      <div class="inputBx">
        <input type="text" placeholder="主题" v-model="title">
      </div>
      <div class="inputBx">
        <input type="text" placeholder="等级" v-model="level">
      </div>
      <div class="inputBx">
        <input type="text" placeholder="开始时间" v-model="start_date">
      </div>
      <div class="inputBx">
        <input type="text" placeholder="结束时间" v-model="end_date">
      </div>
      <div class="inputBx">
        <input type="submit" value="提交" id="post_sub" @click="post_data">
        <input type="submit" value="提交" id="put_sub" @click="put_data">
        <input type="submit" value="取消" @click="showEdit">
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: "dagnjian",
  data (){
    return {
      data: '',
      personal_data: '',
      img_data: '',
      this_id: '',
      title: '',
      level: '',
      start_date: '',
      end_date: '',
    }
  },
  methods: {
    getData(){
      const than = this
      than.axios.get('http://127.0.0.1:8000/dangjian/').then(res =>
      {
        console.log(res.data);
        than.data = res.data
      })
    },
    gotoInfo (id){
      this.$router.push('/admin/dangjian/'+id+'/')
    },
    showEdit(){
      document.getElementById('My_edit').classList.toggle('active')
    },
    show_Post_Edit(){
      document.getElementById('title').innerHTML = '添加'
      this.showEdit()
    },
    show_Put_Edit(){
      document.getElementById('title').innerHTML = '修改'
      this.showEdit()
    },
    post_data(){

    },
    put_data(){

    }
  },
  created (){
    this.getData()
  }
}
</script>

